@media (max-width: 48rem) {
  .gallery-main,
  .gallery-md-top,
  .gallery-md-bottom {
    padding: 0;
  }
}

@media (min-width: 300px) and (max-width: 600px) {
  .mobile-nav {
    display: inline-block;
  }
  .desktop-nav {
    display: none;
  }
  .nav-bar {
    padding   : 0.5% 2%;
  }
}
